.livestream-container {
  display: grid;
  font-weight: 400;
  grid-template-columns:  20% 40% 40%;
  grid-template-rows: auto;
  height: 100vh;
  grid-template-areas: 
    "sidebar chat video";
}
/* Users */
.users-container {grid-area: sidebar;} 
.online-users-title {padding: .2em .8em; margin-bottom: 0;text-transform: uppercase; color: #b0b0b0; font-weight: 500;}
.users-list {list-style: none; padding: 0; margin-top: .4em;}
.username-item {padding: 1em; font-weight: 500; background: #fafafa; text-transform: capitalize; padding: 1em; color: #000; display: flex; justify-content: space-between;}
.my-username-item {background: #fff; font-weight: 400; text-transform: none;}
.my-username-item strong {color: #000;}
.username-item img {cursor: pointer;}
/* Chat */
.chat-container {position: relative;grid-area: chat;background: #eee;}
.send-message-container {position: absolute;bottom: 5%;width: 700px;max-width: 80%;left: 0;right: 0;margin: auto; display: flex;}
.chat-messages {width: 700px;padding: 2em 0;max-width: 80%;margin: auto;}
.chat-message {display: flex; margin-top: -13px; color: #333;}
.my-message { justify-content: flex-end;margin-top: -5px;}
.message-username {font-size: .8em; font-weight: 100; color: #444;}
.message-inner-container {display: inline-block; padding: .4em .8em;border-radius: 10px;}
.chat-message .message-inner-container { background-color: white;}
.my-message .message-inner-container {background-color: #8bc34a8c;}

.chat-btn { font-size: 1.4em; padding: .6em; color: white;background: #009688;border-color: #009688;border-radius: 0px 10px 10px 0px;}
.chat-msg { font-size: 1.4em; padding: .6em; width: 73%; border-radius: 10px 0px 0px 10px; background: white; border: none;}
/* Videostream */
.videostream-container {
  position: relative; grid-area: video;
  max-width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: flex-start;
}
.video-inner-container {
  max-width: 50%;
  /* display: flex; */
  /* flex-wrap: wrap; */
}

#video-container video {
  max-width: 100%;
  box-sizing: border-box;
  padding: .5em;
  border-radius: 20px;
  border: 1px solid #fafafa;
}